footer {
  flex-shrink: 0;
  width: 100%;
  height: 150px;
  background: black;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1.75em;
}

#social-links {
  display: flex;
  gap: 0.5em;
}

#social-links img {
  cursor: pointer;
  border-radius: 50%;
  opacity: 0.75;
  transition: all 150ms ease-out;
}
#social-links img:hover {
  opacity: 1;
  transform: scale(1.05);
}
