.carousel-container {
  height: 600px;
  position: relative;
}

.carousel {
  height: 100%;
  display: flex;
  position: relative;
}

.left-arrow {
  z-index: 10;
  cursor: pointer;
  position: absolute;
  top: 50%;
  width: 60px;
  height: 60px;
  opacity: 0.75;
  border: 3px solid white;
  border-right: 3px solid transparent;
  border-top: 3px solid transparent;
  transform: translate(50%, -50%) rotate(45deg);
}

.right-arrow {
  z-index: 10;

  cursor: pointer;
  position: absolute;
  top: 50%;
  left: 100vw;
  transform: translate(-165%, -50%) rotate(-135deg);
  width: 60px;
  height: 60px;
  opacity: 0.75;
  border: 3px solid white;
  border-right: 3px solid transparent;
  border-top: 3px solid transparent;
}
