nav {
  display: grid;
  grid-template-columns: 4fr 1fr 1fr;
  align-items: center;
  background: black;
  padding: 1.25em 5em;
  z-index: 10;
}

.logo {
  font-size: 1.8em;
  color: white;
  text-decoration: none;
}

.browse {
  height: 35px;
  padding: 0 0.5em;
  font-size: 0.9em;
}

.browse:focus {
  outline: none;
}

.login {
  text-decoration: none;
  color: white;
  cursor: pointer;
  font-size: 1.25em;
  text-align: center;
}
