.quote-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  border: 1px solid gray;
  padding: 2em;
}

.quote {
  font-size: 1.5em;
}

.author {
  font-size: 1.1em;
}

.layer {
  margin-top: 10px;
  display: flex;
  justify-content: space-between;
}

.like {
  cursor: pointer;
}
