.form-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 5em 0;
}

form {
  display: flex;
  flex-direction: column;
  gap: 1em;
}

form h1 {
  margin: 0;
}

form input,
form button {
  width: 250px;
  height: 50px;
  padding: 0 10px;
}

form input:autofill,
form input:valid:not([type="submit"]) {
  background: white !important;
  border: 1px solid rgba(75, 75, 75);
}

form button {
  cursor: pointer;
  position: relative;
  background: none;
  border: 1px solid black;
  font-size: 0.9rem;
  transition: color 250ms ease-out;
}

form button::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: black;
  transform-origin: left;
  transform: scaleX(0);
  transition: transform 250ms ease-out;
  z-index: -1;
}

form button:hover {
  color: white;
}

form button:hover::before {
  transform: scaleX(1);
}

.error {
  color: red;
}
