@import url("https://fonts.googleapis.com/css2?family=Nunito&display=swap");

*,
*::before,
*::after {
  box-sizing: border-box;
  font-family: Nunito;
}

html,
body,
#root {
  margin: 0;
  height: 100%;
  scrollbar-width: thin;
  scrollbar-color: white rgb(10, 10, 10);
  overflow-x: hidden;
}

.hidden {
  display: none !important;
}

.App {
  min-height: 100%;
  display: flex;
  flex-direction: column;
}

main {
  flex: 1 0 auto;
}

input:focus {
  outline: none;
}

#register-link,
#login-link {
  color: black;
}
